<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> fa-angle-left </v-icon>
      </v-btn>
      Command Group
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <command-group-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </command-group-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import IconButton from '../../Common/IconButton.vue';
import CommandGroupGrid from './commandGroupGrid.vue';
import routeConstant from '../../../constants/routes';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';

const baseHref = routeConstant.rfStreamsRoot;
const initialBreadCrumbItem = {
  text: 'Streams',
  disabled: false,
  link: baseHref,
};

export default {
  name: 'command-group',
  components: {
    IconButton,
    CommandGroupGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
        with_command_groups: true,
      },
      gridData: {},
      breadcrumbItems: [initialBreadCrumbItem],
    };
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.streamId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      this.breadcrumbItems.push(initialBreadCrumbItem);
      const streamName = _get(this.gridData, 'extraData.name', '');
      if (streamName) {
        this.breadcrumbItems.push({ text: streamName, align: 'center', disabled: true });
      }
    },
  },
};
</script>
