<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :getDataCb="getDataCb"
    >
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <command-group-form
          v-if="isOpen"
          :isEdit="false"
          :stream="stream"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></command-group-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <command-group-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></command-group-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import CommandGroupForm from './commandGroupForm.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfCommandGroupService } from '../../../services/rfCommandGroupService';

export default {
  name: 'rf-command-group-grid',
  components: { GenericGrid, CommandGroupForm },
  props: ['params', 'getDataCb'],
  data() {
    return {
      service: rfCommandGroupService,
      toolbarTitle: 'Command Group',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    stream() {
      return {
        uuid: this.params.uuid,
      };
    },
  },
};
</script>
