import http from '../axios';
import qs from 'query-string';
import _omit from 'lodash/omit';

class BaseService {
  uri = '';
  constructor(uri) {
    this.uri = uri;
  }
  evaluateUri = (uuid = undefined) => {
    return typeof this.uri === 'function' ? this.uri(uuid) : uri;
  };

  fetch = query => {
    let endpoint = this.evaluateUri();
    let uuid = null;
    if (query && query.hasOwnProperty('id')) {
      uuid = query.id;
      query = _omit(query, 'id');
    } else if (query && query.hasOwnProperty('uuid')) {
      uuid = query.uuid;
      query = _omit(query, 'uuid');
    }

    if (query) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }
    if (uuid) {
      return this.fetchById(uuid, query);
    }
    return http.get(endpoint);
  };

  fetchById = (uuid, query) => {
    let endpoint = this.evaluateUri(uuid);
    if (query) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }
    return http.get(endpoint);
  };

  remove = () => {
    let endpoint = this.evaluateUri();
    return http.delete(endpoint);
  };

  removeById = uuid => {
    let endpoint = this.evaluateUri(uuid);
    return http.delete(endpoint);
  };

  create = body => {
    let endpoint = this.evaluateUri();
    return http.post(endpoint, body);
  };

  patch = (uuid, body) => {
    let endpoint = this.evaluateUri(uuid);
    return http.patch(endpoint, body);
  };

  update = (uuid, body) => {
    let endpoint = this.evaluateUri(uuid);
    return http.put(endpoint, body);
  };
}

export default BaseService;
